.searchBar {
  position: relative;
  width: 100%;
  height: 351px;
  background: linear-gradient(135deg, #155dfc 0%, #1447e6 100%);
  padding-top: 65px;
}

.container {
  max-width: 1536px;
  margin: 0 auto;
  padding: 0 128px;
}

.title {
  text-align: center;
  font-family: 'Inter', 'Noto Sans JP', sans-serif;
  font-size: 36px;
  font-weight: 400;
  line-height: 40px;
  color: #ffffff;
  letter-spacing: 0.3691px;
  margin-bottom: 32px;
}

.searchCard {
  background-color: #ffffff;
  border-radius: 16px;
  box-shadow: 0px 20px 25px -5px rgba(0, 0, 0, 0.1), 0px 8px 10px -6px rgba(0, 0, 0, 0.1);
  padding: 24px;
}

.searchFields {
  position: relative;
  height: 70px;
  display: flex;
  gap: 16px;
}

.field {
  width: 280px;
  display: flex;
  flex-direction: column;
  gap: 8px;
}

.label {
  font-family: 'Inter', 'Noto Sans JP', sans-serif;
  font-size: 14px;
  font-weight: 400;
  line-height: 20px;
  color: #4a5565;
  letter-spacing: -0.1504px;
}

.inputContainer {
  display: flex;
  align-items: center;
  gap: 8px;
  height: 42px;
  border: 1px solid #d1d5dc;
  border-radius: 10px;
  padding: 0 13px;
  background-color: #ffffff;
}

.icon {
  flex-shrink: 0;
  width: 20px;
  height: 20px;
}

.input {
  flex: 1;
  border: none;
  outline: none;
  font-family: 'Inter', 'Noto Sans JP', 'Noto Sans SC', sans-serif;
  font-size: 16px;
  color: #101828;
  background: transparent;
  min-width: 0;
}

.input::placeholder {
  color: rgba(16, 24, 40, 0.5);
  letter-spacing: -0.3125px;
}

.searchButton {
  position: absolute;
  right: 0;
  top: 22px;
  width: 280px;
  height: 48px;
  background-color: #155dfc;
  border-radius: 10px;
  border: none;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 8px;
  cursor: pointer;
  transition: background-color 0.2s;
}

.searchButton:hover {
  background-color: #134ce8;
}

.searchIcon {
  flex-shrink: 0;
  width: 20px;
  height: 20px;
}

.searchButton span {
  font-family: 'Inter', 'Noto Sans JP', sans-serif;
  font-size: 16px;
  font-weight: 400;
  line-height: 24px;
  color: #ffffff;
  letter-spacing: -0.3125px;
}
